import { Fragment, useState } from 'react'
import Link from 'next/link'
import css from 'styled-jsx/css'
import fetch from 'isomorphic-unfetch'
import Button from '@material/react-button'
import Card, {
  CardPrimaryContent,
  CardMedia,
  CardActions,
  CardActionButtons,
  CardActionIcons
} from '@material/react-card'
import MaterialIcon from '@material/react-material-icon'
import { Cell, Grid, Row } from '@material/react-layout-grid'
import Dialog, {
  DialogTitle,
  DialogContent,
  DialogFooter,
  DialogButton,
} from '@material/react-dialog'

const getShrinkingDiv = (shrunk, height) => {
  return css.resolve`
    div {
      transition: height 0.2s ease;
      overflow: hidden;
      height: ${shrunk ? 0 : height}px;
    }
  `
}

const largeShrunk = getShrinkingDiv(true, 300)
const largeUnshrunk = getShrinkingDiv(false, 300)

const smallShrunk = getShrinkingDiv(true, 52)
const smallUnshrunk = getShrinkingDiv(false, 52)

const cardStyles = css.resolve`
  div {
    color: rgba(0,0,0,.7);
  }
`


const dialogStyles = css.resolve`
  div > div.mdc-dialog__container {
    height: 600px;
  }
`




const SelectedMovie = ({ isOpen, show }) => {
  return (
    <Fragment>
      <Dialog
        className={dialogStyles.className}
        open={isOpen}
      >
        <DialogTitle>{show.name}</DialogTitle>
        <DialogContent>
          <img
            src={`https://picsum.photos/id/${610}/400`}
            className='dialog-cover-photo'
          />
          <div>HELLO DIALOG CONTENT</div>
        </DialogContent>
        <DialogFooter>
          <DialogButton action='dismiss'>Dismiss</DialogButton>
          <DialogButton action='accept' isDefault>Accept</DialogButton>
        </DialogFooter>
      </Dialog>
      {dialogStyles.styles}
      <style jsx>{`
        .dialog-cover-photo {
          max-width: 100%;
        }
      `}</style>
    </Fragment>
  )
}


const Movies = ({ shows }) => {
  const [expanded, setExpanded] = useState({})
  return (
    <div className='content-container'>
      <h1>Header</h1>
      <p>This content is in <i>pages/movies.js</i></p>
      <SelectedMovie
        isOpen
        show={shows ? shows[0] : null}
      />
      <Row>
        {shows.map((show, i) => (
          <Cell
            key={show.id}
            desktopColumns={4}
            phoneColumns={4}
            tabletColumns={4}
          >
            <Card
              className={cardStyles.className}
            >
              <CardPrimaryContent
                onClick={e => {
                  setExpanded(x => ({
                    ...x,
                    [show.id]: !expanded[show.id],
                  }))
                }}
              >
                <CardMedia
                  className={expanded[show.id] ? largeShrunk.className : largeUnshrunk.className}
                  square
                  imageUrl={`https://picsum.photos/id/${610+i}/400`}
                />
                <h3>
                  {show.name}
                </h3>
                <div
                  className={[
                    expanded[show.id] ? largeUnshrunk.className : largeShrunk.className,
                    'expanded-content',
                  ].join(' ')}
                >
                  <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Imperdiet proin fermentum leo vel. Porttitor eget dolor morbi non. Sed arcu non odio euismod lacinia at quis risus. Iaculis urna id volutpat lacus laoreet non curabitur gravida arcu. Enim nec dui nunc mattis enim. Id semper risus in hendrerit. Ipsum nunc aliquet bibendum enim facilisis. In aliquam sem fringilla ut morbi tincidunt augue. Vulputate enim nulla aliquet porttitor lacus luctus accumsan tortor. Orci ac auctor augue mauris augue neque gravida in. Mattis vulputate enim nulla aliquet porttitor. Metus dictum at tempor commodo ullamcorper a lacus vestibulum sed. Imperdiet massa tincidunt nunc pulvinar sapien et. Rhoncus mattis rhoncus urna neque.</p>
                  <p>Dolor sit amet consectetur adipiscing elit duis. Lacus suspendisse faucibus interdum posuere lorem ipsum dolor sit. Leo urna molestie at elementum eu. Sed adipiscing diam donec adipiscing tristique risus nec feugiat. At lectus urna duis convallis convallis tellus id. Dapibus ultrices in iaculis nunc sed augue lacus viverra vitae. Nulla pharetra diam sit amet nisl suscipit adipiscing bibendum. Praesent tristique magna sit amet purus gravida quis. Lectus proin nibh nisl condimentum. Nibh venenatis cras sed felis. Consequat semper viverra nam libero justo. Ultrices gravida dictum fusce ut. Enim praesent elementum facilisis leo vel fringilla.</p>
                  <div
                    className='fade-overlay'
                  />
                </div>
              </CardPrimaryContent>
              <div
                className={expanded[show.id] ? smallUnshrunk.className : smallShrunk.className}
              >
                <CardActions>
                  <CardActionButtons>
                    <Link
                      href='/movies/[id]'
                      as={`/movies/${show.id}`}
                    >
                      <Button
                        outlined
                        trailingIcon={<MaterialIcon icon='arrow_forward' />}
                      >
                        Read More
                      </Button>
                    </Link>
                  </CardActionButtons>
                </CardActions>
              </div>
            </Card>
          </Cell>
        ))}
      </Row>
      {largeShrunk.styles}
      {largeUnshrunk.styles}
      {smallShrunk.styles}
      {smallUnshrunk.styles}
      {cardStyles.styles}
      <style jsx>{`
        h3 {
          padding: 0 1em 0 1em;
        }
        .expanded-content {
          position: relative;
          padding: 0 1em 0 1em;
        }
        .fade-overlay {
          position: absolute;
          top: 0px;
          bottom: 0px;
          left: 0px;
          right: 0px;
          background: linear-gradient(to top, #fff, transparent 3em, transparent 100%);
        }
      `}</style>
    </div>
  )
}

Movies.getInitialProps = async function() {
  console.log('getting data')
  const res = await fetch('https://api.tvmaze.com/search/shows?q=batman')
  const data = await res.json()
  console.log(`Show data fetched. Count: ${data.length}`)

  return {
    shows: data.map(entry => entry.show)
  }
}

export default Movies
